import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { MatMenu } from '@angular/material/menu';
import { MatSidenav } from '@angular/material/sidenav';
import { Router, ActivatedRoute } from '@angular/router';
import { AuthService } from '../../../service/auth.service';
import { AccountService } from '../../../service/account.service';
import { FactoryService } from '../../../service/factory.service';

@Component({
  selector: 'app-student',
  templateUrl: './student.component.html',
  styleUrls: ['./student.component.css']
})
export class StudentComponent implements OnInit {

  public studentProfile;
  public saving;
  public saved;
  public photoNote;
  public photo;
  
  constructor(public auth: AuthService, public router:Router, public accountService: AccountService) { 
    this.saving=false;
    this.saved=false;
    this.photoNote=false;
    this.get_student_profile();
  }

  ngOnInit() {
  
  }

  get_student_profile() {
    this.accountService.api_student_profile().then(res => {
      this.studentProfile=res;
      this.photo=this.studentProfile.general_information.avatar;
    });
  }
  
  //保存数据
  save_profile(){
    this.saving=true;
    this.accountService.api_save_student_profile(this.studentProfile).then(res => {
      this.saving=false;
      this.get_student_profile();
      
      this.saved=true;
      setTimeout(()=>{    
        this.saved=false;
      }, 3000);
      
      //保存图片更换session
      if(this.photoNote){
        this.auth.refresh_userinfo();
        this.photoNote=false;
      }
    });
  }
  
  reset_password(){
    if(confirm("Changing your password will take you to the Password Reset Page where you will be asked to confirm your current password and supply a new one.  After doing so, you will be logged out of GuidedPath and will need to login again with your new password. Be sure to save any change you have made on this page before doing so.  Do you want to proceed?")) {
      this.router.navigateByUrl("/account/changePassword");
    }
  }
  
  //更新头像
  uploadFile(event) {
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      reader.readAsDataURL(event.target.files[0]); 
      reader.onload = (event) => {
        this.photo = reader.result;
        this.photoNote=true;
        this.studentProfile['user_photo']=reader.result;
      }
    }
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
}
